    width: 100%;
}
.sildebar[_v-308d30e5] {
    display: block;
    width: 100%;
    height: 10px;
    position:relative;
    background-color: #eee;
    border-radius: 5px;
}
.sildeleft[_v-308d30e5] {
    display: block;
    height: 100%;
    background-color: green;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    border-radius: 5px;
}
.sildepoint[_v-308d30e5] {
    position: absolute;
    top: -5px;
    width: 10px;
    height: 10px;
    display: block;
    cursor: pointer;
    background-color: green;
    border-radius: 50%;
    z-index: 3;
    border: 5px solid #fff;
}



