
































































































input,button,select,textarea{outline:none;}
html {
  height: 100%;
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

#app {
  color: #2c3e50;
  width: 320px;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
}

#app a {
  color: #42b983;
  text-decoration: none;
}

.logo {
  width: 100px;
  height: 100px
}
.test-box {
    width: 100%;
    background-color: #eee;
}
.test-btn {
    width: 70%;
    height: 28px;
    background-color: #05AE87;
    border-radius: 15px;
    border-width: 0;;
    cursor: pointer;
    margin-bottom: 2px;
}
.input{
    width: 100%;
    height: 35px;
    position: relative;
}
.input label{
  font-size: 0.9rem;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  min-height: 100%;
  height: 35px;
  line-height: 35px;
  display: block;
  position: absolute;
  top: 0px;
  left: 12%;
}
.input input{
    position: absolute;
    top: 0px;
    right: 12%;
    border-radius: 15px;
    text-align: center;
    transition: width 0.5s;
    -moz-transition: width 0.5s;  /* Firefox 4 */
    -webkit-transition: width 0.5s; /* Safari 和 Chrome */
    -o-transition: width 0.5s;
    width: 50%;
    height: 80%;
    /*display: block;*/
    margin: 0 auto;
    border: 1px solid #ccc;
    opacity: 0.7;
}
.input input:focus{
  width: 55%;
  transition: width 0.5s;
  -moz-transition: width 0.5s;  /* Firefox 4 */
  -webkit-transition: width 0.5s; /* Safari 和 Chrome */
  -o-transition: width 0.5s;
  -webkit-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  border:none;
  outline:medium;
  opacity: 1;
}



